<template>
<div class="pokemon-container">
  <img
    :src="imgSrc"
    class="hidden-pokemon"
    alt="Pokemon"
  />
  <img
    v-if="imgpoke"
    :src="imgSrc"
    class="fade-in"
    alt="Pokemon"
  />

</div>
</template>

<script>
export default {
  props:{
    pokemonId:{

      type:Number,
     required:true,
    },
    imgpoke:{
      type:Boolean,
      required:true,
      default:false

    }
  },
   computed:{
     imgSrc(){
       console.log(this.pokemonId);
       return `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/other/dream-world/${this.pokemonId}.svg`
     }
   }


};
</script>

<style scoped>


/* Pokemon Picture */
.pokemon-container {
    height: 200px;
}
img {
  height: 200px;
  position: absolute;
  width: 100vw;
  margin-left: -50vw;
  user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
}
.hidden-pokemon {
    filter: brightness(0);
}
</style>
